/* Login form container */
.login-container {
    max-width: 400px;
    padding: 20px;
    background-color: #b96666;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* Form inputs */
  .form-input {
    width: 100%; /* Adjust width to fill the container */
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  /* Submit button */
  .submit-button {
    width: 100%; /* Adjust width to fill the container */
    padding: 8px; /* Decrease padding to make the button smaller */
    font-size: 14px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submit-button:hover {
    background-color: #0056b3;
  }
  