/* Styling for the container */
.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* Styling for headings */
  h2 {
    color: #333;
  }
  
  p {
    color: #666;
  }
  
  /* Styling for the logout button */
  button {
    padding: 5px 5px;
    font-size: 16px;
    background-color: #c9404e;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  /* Styling for the login link */
  a {
    display: inline-block;
    margin-top: 10px;
    padding: 8px 16px;
    font-size: 14px;
    text-decoration: none;
    color: #007bff;
    border: 1px solid #007bff;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  a:hover {
    background-color: #007bff;
    color: #fff;
  }
  